@font-face {
  font-family: "Bloggersans Light";
  src: url("../fonts/Bloggersanslight.woff2") format("woff2"), url("../fonts/Bloggersanslight.woff") format("woff");
  font-weight: 200; }

@font-face {
  font-family: "Bloggersans Regular";
  src: url("../fonts/Bloggersans.woff2") format("woff2"), url("../fonts/Bloggersans.woff") format("woff");
  font-weight: 400; }

@font-face {
  font-family: "Bloggersans Medium";
  src: url("../fonts/Bloggersansmedium.woff2") format("woff2"), url("../fonts/Bloggersansmedium.woff") format("woff");
  font-weight: 600; }

@font-face {
  font-family: "Bloggersans Bold";
  src: url("../fonts/Bloggersansbold.woff2") format("woff2"), url("../fonts/Bloggersansbold.woff") format("woff");
  font-weight: 800; }

body {
  margin: 0; }

ul, ol {
  margin-block-start: 0;
  margin-block-end: 0;
  list-style: none;
  padding-inline-start: 0; }

p {
  cursor: default; }

/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */
html {
