.pop-up--blackout {
  background-color: rgba(0, 0, 0, 0.8); }

.pop-up {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  z-index: 80; }

.pop-up__shadow {
  box-shadow: 0px 0px 2px 0 #000712; }

.pop-up__window {
  background-color: #f2e2dd;
  color: #f2e2dd;
  width: 310px;
  font-family: "BloggerSans Regular", "Arial", sans-serif;
  font-size: 16px;
  border-radius: 10px;
  cursor: default;
  position: absolute;
  user-select: none; }

.pop-up__noDisplay {
  display: none; }

.pop-up__nav {
  cursor: pointer;
  position: absolute;
  right: 0;
  height: 40px;
  width: 40px;
  background-color: #f89554;
  border-radius: 3.33333px; }

.pop-up__nav--close {
  top: -20px; }

.pop-up__nav--back {
  bottom: -20px; }

.pop-up__button-svg {
  stroke: #f2e2dd;
  fill: #f2e2dd; }

.pop-up__nav:hover {
  background-color: #f89554; }

.pop-up__wrapper {
  background-color: #00213a;
  width: 100%;
  border-radius: 10px; }

.pop-up__logo {
  display: none;
  height: 48px;
  width: 48px;
  position: absolute;
  background-color: #f2e2dd;
  border-radius: 3.33333px;
  top: -25px;
  left: -25px; }

.pop-up__logo-svg {
  display: none;
  width: 36px;
  height: 36px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -18px;
  margin-top: -18px;
  fill: #426c79; }

@media (min-width: 768px) {
  .pop-up__window {
    width: 700px;
    font-size: 22px; }
  .pop-up__nav {
    right: -10px;
    background-color: #f89554; }
  .pop-up__nav--close {
    top: -10px; }
  .pop-up__nav--back {
    bottom: -10px; }
  .pop-up__wrapper {
    width: 80%; } }

@media (min-width: 1440px) {
  .pop-up__window {
    width: 900px;
    font-size: 24px; }
  .pop-up__wrapper {
    width: 75%; }
  .pop-up__logo {
    display: block; }
  .pop-up__logo-svg {
    display: block; } }

.pop-up__text {
  margin: 0;
  padding: 25px 10px 0 10px; }

.pop-up__dark-text {
  color: #426c79; }

.pop-up__list {
  padding-top: 30px;
  padding-left: 10px;
  padding-bottom: 50px; }

.pop-up__buttons-list {
  padding-left: 5%;
  padding-right: 5%; }

.pop-up__list-item:not(:last-child) {
  margin-bottom: 25px; }

.pop-up__button {
  cursor: pointer;
  background-color: #f2e2dd;
  height: 40px;
  width: 100%;
  text-align: center;
  border: none;
  border-radius: 5px;
  font-family: "BloggerSans Medium", "Arial", sans-serif; }

.pop-up__button:hover {
  background-color: #f89554; }

@media (min-width: 768px) {
  .pop-up__text {
    padding: 25px 20px 0 20px; }
  .pop-up__list {
    padding-top: 40px;
    padding-left: 30px;
    padding-bottom: 40px; }
  .pop-up__buttons-list {
    padding-left: 15%;
    padding-right: 15%; }
  .pop-up__button {
    height: 45px;
    width: 100%; }
  .pop-up__list-item:not(:last-child) {
    margin-bottom: 25px; } }

@media (min-width: 1440px) {
  .pop-up__text {
    padding: 25px 40px 0 40px; }
  .pop-up__list {
    padding-top: 40px;
    padding-left: 60px;
    padding-bottom: 40px; }
  .pop-up__buttons-list {
    padding-left: 20%;
    padding-right: 20%; }
  .pop-up__list-item:not(:last-child) {
    margin-bottom: 30px; } }

.info {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  z-index: 100;
  height: 100%; }

.info__noDisplay {
  display: none; }

.info__popup {
  background-color: #00213a;
  color: #f2e2dd;
  width: 290px;
  font-family: "BloggerSans Regular", "Arial", sans-serif;
  font-size: 17.6px;
  border-radius: 10px;
  cursor: default;
  position: absolute;
  user-select: none; }

.info__text {
  margin: 0;
  padding: 25px 10px 25px 10px; }
